.p-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: justify;
  margin: 0;
  margin-bottom: 20px;
  width: 400px;
  background-image: linear-gradient(to top, #7028e4 0%, #e5b2ca 100%);
}

.img-section img {
  width: 400px;
}

.text-content {
  padding: 10px 20px;
}

