@media only screen and (max-width: 1080px) {

  .scene {
    display: none;
  }

  .f-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .p-card {
    margin-top: 30px;
    width: 100%;
  }

  .p-card img {
